import "./App.css";
import Nav from "./my components/Nav";
import Footer from './my components/Footer';
import Textbox from "./my components/Textbox";
// import Counter from "./my components/Counter";
import About from "./my components/About";
import React, { useState } from "react";
import Alert from "./my components/Alert"; 
// import Youtube from "./my components/Youtube";
import {BrowserRouter as Router, Switch, Route, Link} from "react-router-dom";
import Home from "./my components/Home";
function App() {
  const [mode, setmode] = useState("light");
  const [lbtext, setlbtext] = useState("Enable Dark Mode");
  const [lc, setlc] = useState("");
  const [alert, setAlert] = useState(null); 
  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      type: type,
    });
    setTimeout(() => {
      setAlert(null);
    }, 1000);
  };
  const toggle = () => {
    if (mode === "light") {
      setmode("dark");
      document.body.style.background = "black";
      document.body.style.color = "white";
      setlbtext("Disable Dark Mode");
      setlc("light");
      showAlert("dark mode is enable", "danger");
      document.title=("My First React App - in dark mode");
      setTimeout(() => {
        document.title=("set timeout changed me");
      }, 2000);
    } else {
      setmode("light");
      document.body.style.background = "white";
      document.body.style.color = "black";
      setlbtext("Enable Dark Mode");
      setlc("dark");
      showAlert("light mode is enable", "primary");
      document.title=("My First React App - in light mode");
      setTimeout(() => {
        document.title=("MY AWESOME REACT");
      }, 1000);
    }
  };
  const danger = () => {
    document.body.style.background = "red";
    document.body.style.color = "white";
  };
  const warning = () => {
    document.body.style.background = "orange";
    document.body.style.color = "white";
  };

  const info = () => {
    document.body.style.background = "skyblue";
    document.body.style.color = "white";
  };
  return (
    <>
    {/*HERE I AM USING REACT ROUTER WHICH IS USED FOR JUMPING ON DIFFERENT PAGES WITHOUT
    RELOADING OUR PAGE...(THIS IS A VERY SPECIAL FEATURE OF REACT WHICH MAKES IT SO FAST)*/}
    <Router>
      <div className="App">
        {/* {
      <Footer missedtext="'''I AM MISSED TEXT IN FOOTER COMPONENT AND I CAME HERE WITH THE HELP OF PROPS'''"/>
      <div className="container">
      <Counter/>  
      </div>
    } */}
        <Nav
          title="202(PROPS CHANGED ME)"
          mode={mode}
          lb={lbtext}
          lt={lc}
          toggler={toggle}
          About="About"
          // Contact={3092795836}
          d={danger}
          w={warning}
          i={info}
          footer="Footer"
          textbox="Textbox"
        />
      <Switch>
          {/*THIS /about exact path TAKES US TO THE About PAGE)*/}
                              {/*REMEMBER THAT MY "About ROUTE PAGE" ONLY WORKS WHEN "ITS
                              ON THE TOP OF MY "/" (Home Component)"..I DONT KNOW WHY THIS
                              HAPPENS BUT I WILL SEE LATER THAT WHY THIS HAPPEN...
                              "BUT RIGHT NOW ITS IMPORTANT"...*/}
          <Route exact path="/about">
            <About/>
          </Route>
          <Route exact path="/textbox">
          <Textbox alert={showAlert} heading="HEY THIS IS OUR TEXT BOX" />
          </Route>
                {/* "JUST FOR BETTER" PRACTICE WE CAN USE "exact exact path" instead of "exact path"...(ITS "NOT NECESSARY" BUT IT IS "USEFULL" WHEN WE HAVE "LARGE NUMBER OF ROUTER COMPONENTS)" */}
                 {/* WHETHER I TYPE HERE footer OR Footer OR FOOTER IT WILL WORK...(THIS RULE IS APPLICABLE FOR ALL MY Route COMPONENTS) */}
          <Route exact path="/footer"> 
            <Footer/>
          </Route>
          {/*THIS / exact path WORKS LIKE INDEX FILE (WHICH IS OUR FIRST PAGE OF A WEBSITE)*/}
          <Route exact path="/">
            <Home/>
          </Route>
        </Switch>
      </div>
      </Router>
    </>
  );
}

export default App;
